import React, { useEffect } from 'react'
import { ChatState } from '../Context/chatProvider'
import { Box, Button, Icon, InputGroup, Spacer, Stack, Text, useToast } from '@chakra-ui/react'

import axios from 'axios';
import { AddIcon } from '@chakra-ui/icons';
import {  getSenderFull, showMessages } from '../config/ChatLogics';
import ChatLoading from './ChatLoading';
import GroupChatModal from './Authentication/miscellaneous/GroupChatModal';

const MyChats = ({fetchAgain}) => {
 
const {user,chats,setChats,selectedChat,setSelectedChat,loggedUser,setLoggedUser,isOnline,setIsOnline}=ChatState()
const toast=useToast()


useEffect(()=>{
 setLoggedUser(JSON.parse(localStorage.getItem("userInfo")));
  const fetchChats= async()=>{
  try {
    const config={
      headers:{
       
        Authorization: `Bearer ${user.token}`,
      },
    };
  const {data} =await axios.get("/api/chat",config);
  
  setChats(data);
    
   
    
  } catch (error) {
    toast({
          title: 'Error Occured.',
          description:"failed to load chat",
          status: 'error',
          duration: 5000,
          isClosable: true,
          position:'bottom-left',
        });
  }
}
  
  fetchChats();
},[fetchAgain])

  return (
    <Box
    display={{ base: selectedChat ? "none" :"flex",md:"flex"}}
    flexDirection="column"
    alignItems="center"
    p={3}
    bg="white"
    w={{base:"100%" ,md:"31%"}}
    borderRadius="lg"
    borderWidth="1px"
    >
          <Box
       display="flex"
       pb={3}
       px={3}
       w="100%"
       fontSize={{base:"28px",md:"30px"}}
       fontFamily="Work sans"
       justifyContent="space-between"
       alignItems="center"
       >
        My Chats
    <GroupChatModal>
      <Button
      display="flex"
      fontSize={{base:"17px",md:"10px" ,lg:"17px"}}
      rightIcon={<AddIcon/>}
      >
        New Group Chat
      </Button>
     </GroupChatModal>
      </Box>
      <Box
      display="flex"
      flexDirection="column"
      overflow="hidden"
      w="100%"
      h="100%"
      p={3}
      borderRadius="lg"
      background="#F8F8F8"
      >
        
        {chats ? (
          <Stack overflowY="scroll"> 
            { chats.map((chat)=>(
              <Box
              onClick={()=>setSelectedChat(chat)}
              cursor="pointer"
              bg={selectedChat === chat ? "#38B2AC" :"#E8E8E8"}
                  color={selectedChat === chat ? "white" :"black"}
                  px={3}
                  py={2}
                  borderRadius="lg"
                  key={chat._id}
              >
                  <InputGroup>
                  
                <Text>
                  {!chat.isGroupChat?(getSenderFull(loggedUser,chat.users).name
                 
                  )
                  :(chat.chatName)}
                  

                </Text>
                
                <Spacer />
                    <Text display="flex" > 
                    {getSenderFull(loggedUser,chat.users).isOnline ?  
                    
                    <Icon viewBox='0 0 200 200' color='green'>
                     <path
                    fill='currentColor'
                         d='M 100, 100 m -75, 0 a 75,75 0 1,0 150,0 a 75,75 0 1,0 -150,0'
                      />
                     </Icon>
                   :
                       <Icon viewBox='0 0 200 200' color='red'>
                     <path
                    fill='currentColor'
                         d='M 100, 100 m -75, 0 a 75,75 0 1,0 150,0 a 75,75 0 1,0 -150,0'
                      />
                     </Icon>}
                     </Text>
                
                  </InputGroup>
                  <Text fontSize='4xs' as='b' mr={2}>
                     Last Message :
                    </Text>
                  {chat.hasOwnProperty('latestMessage')?(showMessages(chat.latestMessage.content)):<></>}
                    
               
              </Box>

            ))}
          </Stack>
        ):(
          <ChatLoading/>
        )}
      </Box>
    </Box>
  )
}

export default MyChats
