import React from 'react'
import ScrollableFeed from 'react-scrollable-feed'
import { ChatState } from '../Context/chatProvider'
import { isLastMessage, isSameSender, isSameSenderMargin, isSameUser } from '../config/ChatLogics'
import {  Avatar,  Box,  Button, IconButton, Image, Tooltip } from '@chakra-ui/react'
import { DownloadIcon } from '@chakra-ui/icons'
import ReactPlayer from 'react-player/lazy'

const ScrollableChat = ({messages}) => {
    const {user} =ChatState()

  const downloadImage=(url)=>{
      console.log(url);
      var link=document.createElement('a');
      document.body.appendChild(link);
      link.href=url;
      link.target='_blank';
      link.click();
      return;
  }

  return (
    <ScrollableFeed>
      {messages && messages.map((m,i)=>(
        <div style={{ display:"flex"}} key={m._id} >
            {
                (isSameSender(messages,m,i,user._id)
                || isLastMessage(messages,i,user._id)
                ) && (
                    <Tooltip
                    label={m.sender.name}
                    hasArrow
                    placement='bottom-start'
                    >
                      <Avatar
                        mt="7px"
                        mr={1}
                        size="sm"
                        cursor="pointer"
                        name={m.sender.name}
                        src={m.sender.pic}
                        />
                    </Tooltip>
                )

             }

            <span style={{background:`${(m.type === 'text')?((m.sender._id===user._id ) ? '#BEE3F8' :'#B9F5D0' ):('none')}`,
                      borderRadius:"20px",
                      padding:"5px 15px",
                      maxWidth:"75%",
                      marginLeft:isSameSenderMargin(messages,m,i,user._id) ,
                      marginTop:isSameUser(messages,m,i,user._id) ? 3 :10,

            }}>
            {m.type === "text" ?m.content :(m.type=== "image" ?<Box m={1} width="100%" className='container'>
            <Image src={m.content} alt='errors'  boxSize='200px' objectFit='cover' className='img'  />
            <IconButton onClick={()=>downloadImage(`${m.content}`)} icon={<DownloadIcon/>} className='iconbtn' />
                      
            </Box> :
            <Box m={1} >
             
               <ReactPlayer url={m.content} controls width={200} height={150}  />
               
              </Box>
            )
               
              }
        </span>
        
        </div>
      ))}
    </ScrollableFeed>
  )
}

export default ScrollableChat
