import React, { useEffect, useState } from 'react'
import { ChatState } from '../Context/chatProvider'
import { Box, Button, FormControl, IconButton, Input, InputGroup, Spinner, Text, Toast, useToast } from '@chakra-ui/react'
import { ArrowBackIcon, ChatIcon, LinkIcon, PhoneIcon, SearchIcon } from '@chakra-ui/icons'
import { getSender, getSenderFull } from '../config/ChatLogics'
import ProfileModal from './Authentication/miscellaneous/ProfileModal'
import UpdateGroupChatModal from './Authentication/miscellaneous/UpdateGroupChatModal'
import axios from 'axios'
import './styles.css';
import ScrollableChat from './ScrollableChat'
import io from 'socket.io-client'
import Lottie from 'react-lottie'
import animationData from "../animation/typing.json"

import FileBadgeItem from './UserAvatar/FileBadgeItem'

 //const ENDPOINT = 'http://127.0.0.1:5000/';
const ENDPOINT = 'https://gutar-goo.onrender.com/';

var socket, selectChatCompare;

const SingleChat = ({fetchAgain , setFetchAgain }) => {

const {user , selectedChat , setSelectedChat , notification , setNotification,isOnline } = ChatState();



  const [messages, setMessages] = useState([])
  const [sender, setSender] = useState()
  const [content, setContent] = useState()

  const [loading, setLoading] = useState(false)
  const [newMessage, setNewMessage] = useState()
  const [picSet, setPicSet] = useState()
 const [socketConnected, setSocketConnected] = useState(false)
 const [typing, setTyping] = useState(false)
 const [isTyping, setIsTyping] = useState(false)
 const [isSelectedFile, setIsSelectedFile] = useState(false)
 const [selectedFile, setSelectedFile] = useState(false)
  const [isUpload, setIsUpload] = useState(false)
  const [pic,setPic] =useState()
  const [contentType,setContentType] =useState("text")
  const [emoji,setEmoji] =useState(false)
const [chosenEmoji, setChosenEmoji] = useState(null);

 const toast =useToast();

 const defaultsOptions ={
    loop:true,
    autoplay:true,
    animationData:animationData,
    rendererSettings:{
        preservedAspectRatio: "xMidYMid slice",
    },
 };



 
const fetchMessages = async () =>{
    if(!selectedChat) return;
    try {
         const config={
                headers:{
                   
                    "Authorization":`Bearer ${user.token}`,
                }
            };
            setLoading(true);
        const {data} =await axios.get(`/api/message/${selectedChat._id}`,config);
     
        setMessages(data);
        setLoading(false);    
        socket.emit("join chat",selectedChat._id);

    } catch (error) {
         Toast({
                title:"Error Occured.",
                description:"Failed to send Message ",
                status:"error",
                duration:5000,
                isClosable:true,
                position:"bottom",
            });
    }
};





  const sendMessage = async (event)=>{
    if(event.key === "Enter" && newMessage && contentType === 'text')
    {
        socket.emit("stop typing",selectedChat._id);
        try {
            const config={
                headers:{
                    "content-type" :"application/json",
                    "Authorization":`Bearer ${user.token}`,
                }
            };

            const {data} = await axios.post('/api/message',{
                content:newMessage,
                type:contentType,
                chatId:selectedChat._id,
            },config);

            socket.emit("new message",data);

            console.log(data);
           
            setNewMessage("");
            setMessages([...messages,data]);
            setContentType("text");

        } catch (error) {
            Toast({
                title:"Error Occured.",
                description:"Failed to send Message ",
                status:"error",
                duration:5000,
                isClosable:true,
                position:"bottom",
            });
        }
    }else if(event.key === "Enter" && newMessage && contentType === 'image'){
         socket.emit("stop typing",selectedChat._id);
        try {
            const config={
                headers:{
                    "content-type" :"application/json",
                    "Authorization":`Bearer ${user.token}`,
                }
            };

            const {data} = await axios.post('/api/message',{
                content:pic,
                type:contentType,
                chatId:selectedChat._id,
            },config);

            socket.emit("new message",data);

            console.log(data);
            setNewMessage("");
            setMessages([...messages,data]);
            setContentType("text");
            setPic("");

        } catch (error) {
            Toast({
                title:"Error Occured.",
                description:"Failed to send Message ",
                status:"error",
                duration:5000,
                isClosable:true,
                position:"bottom",
            });
        }

    }else{
          socket.emit("stop typing",selectedChat._id);
        try {
            const config={
                headers:{
                    "content-type" :"application/json",
                    "Authorization":`Bearer ${user.token}`,
                }
            };

            const {data} = await axios.post('/api/message',{
                content:pic,
                type:contentType,
                chatId:selectedChat._id,
            },config);

            socket.emit("new message",data);

            console.log(data);
            setNewMessage("");
           
            setMessages([...messages,data]);
            setContentType("text");
             setPic("");
        } catch (error) {
            Toast({
                title:"Error Occured.",
                description:"Failed to send Message ",
                status:"error",
                duration:5000,
                isClosable:true,
                position:"bottom",
            });
        }

    }
  };

useEffect(()=>{
socket=io(ENDPOINT,{auth:{token:user._id}});
socket.emit("setup",user);
socket.on("connected",()=>setSocketConnected(true));
socket.on("typing",()=>setIsTyping(true))
socket.on("stop typing",()=>setIsTyping(false))
},[]);

const sendNotify = async(content)=>{
    console.log("send Notify");
    try {
        const config={
            headers:{
                "content-type" :"application/json",
                 "Authorization":`Bearer ${user.token}`,
                
            }
        };
    
        const { data } = await axios.post('/api/user/notification/added',{
            
            userId:user._id,
            message:content,
        },config);

        console.log("----out notify");
        
    } catch (error) {
        Toast({
            title:"Error Occured.",
            description:"Failed to send Message ",
            status:"error",
            duration:5000,
            isClosable:true,
            position:"bottom",
        });
    }
}

useEffect(()=>{
sendNotify(content);
},[notification])

useEffect(() => {
  fetchMessages();
  selectChatCompare = selectedChat;
}, [selectedChat]);

console.log(notification,"----------------------------")

useEffect(()=>{
    socket.on("message received",async(newMessageReceived)=>{
        if(!selectChatCompare || selectChatCompare._id !== 
            newMessageReceived.chat._id)
            {
                console.log("give notification -----");
                console.log(newMessageReceived);
               
                 setContent(newMessageReceived);
                // give notification
           if(!notification.includes(newMessageReceived))
           {
            setNotification([newMessageReceived , ...notification]);
            setFetchAgain(!fetchAgain);
                      
           }
          

            }else{
               
              
                setMessages([...messages,newMessageReceived]);
            }
    })
});

  const typingHandler =(e)=>{
    
   if(isUpload)
   {setSelectedFile(e.target.files[0])
   setIsSelectedFile(true);
   setIsUpload(false);
   }else{
       
             
       setNewMessage(e.target.value);
       
       // Typing Indicator Logic
       
       if(!socketConnected) return;
       if(!typing)
       {
           setTyping(true);
           socket.emit("typing",selectedChat._id);
        }
        let lastTypingTime = new Date().getTime();
        var timerLength = 3000;
        setTimeout(() => {
            var timeNow = new Date().getTime();
            var timeDiff = timeNow - lastTypingTime;
            
            if(timeDiff >= timerLength && typing)
            {
                socket.emit("stop typing",selectedChat._id);
                setTyping(false);
            }
        }, timerLength);
        
    }
  };

const openFiles=()=>{
   const input=document.getElementById('searchFile')
   input.click();
//    setContentType("media");
      
}
 const uploadFiles=(event)=>{
    setIsSelectedFile(event.target.files[0])
    setPicSet(event.target.files[0].name);
    // setNewMessage(event.target.files[0].name);
    postDetails(event.target.files[0]);
 } 

const postDetails =(pics)=>{
setLoading(true)
if(pics === undefined)
{
  toast({
    title:"Please select an Image",
    status:"warning",
    duration:5000,
    isClosable:true,
    position:"bottom",

  })
return;
}
if(pics.type === "image/jpeg" || pics.type === "image/png" )
{
  const data =new FormData();
  data.append("file",pics);
  data.append("upload_preset","Chat-App");
  
  fetch("https://api.cloudinary.com/v1_1/techsoftcrews-software-solutuion-pvt-ltd/image/upload",{
    
    method:"post",
    body:data,
  }).then((res) => res.json())
  .then((data)=>{
    setPic(data.url.toString());
    setContentType("image");
    setLoading(false);
  }).catch((err)=>{
    console.log(err);
    setLoading(false);
  })
}else if(pics.type === "video/mp4" || pics.type === "video/ogg"){
  console.log("pics-------------------",pics.size);
  if(pics.size >=10000000)
  {
     toast({
          title: 'File size should be less than 10 MB.',
          status: 'warning',
          duration: 5000,
          isClosable: true,
          position:"bottom",
        })
        setNewMessage("");
        setLoading(false)
        return;
  }

 const data =new FormData();
  data.append("file",pics);
  data.append("upload_preset","Chat-App");
  
  fetch("https://api.cloudinary.com/v1_1/techsoftcrews-software-solutuion-pvt-ltd/video/upload",{
    
    method:"post",
    body:data,
  }).then((res) => res.json())
  .then((data)=>{
    setPic(data.url.toString());
    
    setLoading(false);
    setContentType("media")
  }).catch((err)=>{
    console.log(err);
    setNewMessage("");
    setLoading(false);
  })

}else{
   toast({
          title: 'Please select an Image.',
          status: 'warning',
          duration: 5000,
          isClosable: true,
          position:"bottom",
        })
        setNewMessage("");
        setLoading(false);
        return;
}
}


 const handleDelete=()=>{
    setPic("");
    setNewMessage("");
   
  };

  return (
    <>
     { selectedChat ? (
        <>
        <Text
        fontSize={{base:"28px",md:"25px"}}
        pb={3}
        px={2}
        w="100%"
        fontFamily="Work sans"
        display="flex"
        justifyContent={{base:"space-between"}}
        alignItems="center"
        >
        <IconButton
        display={{base:"flex",md:"none"}} 
        icon={<ArrowBackIcon />}
        onClick={()=>setSelectedChat("")}
        />
        {!selectedChat.isGroupChat ?(
            <>{getSender(user,selectedChat.users) }
            <ProfileModal user={getSenderFull(user,selectedChat.users)} />
            </>
        ):(
            <>{selectedChat.chatName.toUpperCase()}
            {
                <UpdateGroupChatModal 
                fetchAgain={fetchAgain} 
                setFetchAgain={setFetchAgain}
                fetchMessages={fetchMessages}/>
            }
            </>
        )}
         </Text>
        <Box
        display="flex"
        flexDirection="column"
        justifyContent="flex-end"
        p={3}
        bg="#E8E8E8"
        overflowY="hidden"
        w="100%"
        h="100%"
        borderRadius="lg"

        >
            {loading ? (
                <Spinner 
                size="xl"
                w={20}
                h={20}
                alignSelf="center"
                margin="auto" />
            ):(
            <div className='messages'>
                <ScrollableChat messages={messages} />
            </div>
            )
            }
        <FormControl onKeyDown={ sendMessage } isRequired mt={3}>
            {
                isTyping ? <div>
                    <Lottie
                    width={70}
                    style={{ marginTop: 15, marginLeft :0}} 
                    options={defaultsOptions}
                    />
                
                </div> :(<></>)
            }
            <Box w="100%" display="flex" flexWrap="wrap" >
            
            {pic && picSet && <FileBadgeItem 
              file={picSet}
              handleFunction={()=>handleDelete()}
            />}
             </Box>
            <InputGroup>
            <IconButton mr={1} icon={<LinkIcon/>} colorScheme='teal'
            onClick={openFiles}
            />
          
            <Input type='file' onChange={uploadFiles} display="none" id="searchFile" accept="image/*,video/*"/>
            <Input  variant="filled" bg="#E0E0E0" placeholder="Enter a message..."
            onChange={typingHandler} 
            value={newMessage} 
             />
            
             </InputGroup>
        </FormControl>

        </Box>
       
        </>
     ) :
        (
            <Box
            display="flex" alignItems="center" justifyContent="center" h="100%"
            >
                    <Text fontFamily="Work sans" fontSize="3xl" pb={3}
                    >
                        Click On Users To Start Chatting.
                    </Text>
            </Box>
        )
     } 
    </>
  )
}

export default SingleChat
