import { CloseIcon } from '@chakra-ui/icons'
import { Box } from '@chakra-ui/react'
import React from 'react'
import { showFileName } from '../../config/ChatLogics'

const FileBadgeItem = ({file,handleFunction}) => {
  return (
    <Box
    px={2}
    py={1}
    borderRadius="lg"
    m={1}
    mb={2}
    variant="solid"
    fontSize={12}
    backgroundColor="purple"
    color="white"
    cursor="pointer"
    onClick={handleFunction}
    >
      {showFileName(file)}
      <CloseIcon pl={1} />
    </Box>
  )
}

export default FileBadgeItem
